.newsletter
{
    width: 65%;
    height: 40vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    background:linear-gradient(180deg,#FFDAB9, #FFFFFF 60%);
    gap:30px;
}
.newsletter h1
{
    color: #454545;
    font-size: 55px;
    font-weight: 600;
}
.newsletter1
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: white;
    width: 730px;
    height: 70px;
    border-radius: 80px;
    border:1px solid #e3e3e3;
}
.email
{
    width: 500px;
    margin-left: 30px;
    border: none;
    outline:none;
    color: #616161;
    font-size: 16px;
}
.btn1
{
    width:210px;
    height: 70px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 16px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}
.btn { 
    width:210px;
    height: 70px;
    border-radius: 80px;
    background: black;
    color: white;
    font-size: 16px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer; 
    outline: none; 
    transition: 0.2s all; 
    
} 
/* Adding transformation when the button is active */ 
 
.btn:active { 
    transform: scale(0.81); 
    box-shadow: 3px 2px 22px 1px rgba(0, 0, 0, 0.24); 
} 